// eslint-disable-next-line no-unused-vars
function strParam(param, jsonDump=true) {
    return (
        '?'+Object.entries(param).map(
            ([k, v]) => v?`${k}=${(jsonDump? JSON.stringify(v) : v).replace('&', 'ï¼†')}&`:''
        ).join('').slice(0, -1)
    )
}

